<template>
  <div class="Basicdata"> 
    <choosebar @chooseClick="datachange" :titles='tit'></choosebar>
    <basicitem v-for="(item,idx) in goodsList[currentType].list" :goodsItem="item" :key="idx"></basicitem>
  </div>
</template>


<script>
//a
import { getHomeMultidata, getHomeData } from "../../../network/home.js";
import basicitem from './Basicitem.vue'
import choosebar from './Choosebar.vue'

export default {
  name: "Basicdata",
  components: {
    basicitem,
    choosebar
  },
  data() {
    return {
      goodsList: {
        'pop': { page: 1, list: [] },
        'new': { page: 1, list: [] },
        'sell': { page: 1, list: [] }
      },   
      tit: ['流行','新款','精选'],
      currentType: 'pop'   
    };
  },
  created() {  //组件创建结束行
    this.getdata('pop');    
    this.getdata('new');
    this.getdata('sell');
  },
  methods: {
    getdata(type) {
      const page = this.goodsList[type].page + 1
      getHomeData(type, page).then(res => {
        //console.log(res);
        this.goodsList[type].list.push(...res.data.list);  //结构数组然后push到固定位置去
        //console.log(this.goodsList[type].list);
        //console.log(this.goodsList[type].list[1].clientUrl);
        //console.log(this.goodsList[type].list[1].acm);
      });
    },
    datachange(index) {
      //console.log(index);
      switch(index) {
        case 0:
          this.currentType = 'pop'
          break
        case 1: 
          this.currentType = 'new'
          break
        case 2:
          this.currentType = 'sell'
          break;
      }
    }
  }
};

</script>


