<template>
  <div class="home">
    <div id="nav">
      <topbar></topbar>
      <searchbar></searchbar>
      <maincontent></maincontent>      
      <backtop></backtop>
    </div>    
  </div>
</template>


<script>
import topbar from "./Topbar.vue";
import searchbar from "./Searchbar.vue";
import maincontent from "./maincontent/Maincontent.vue";
import backtop from "../Backtop.vue";
import { constants } from 'zlib';

export default {
  name: "Home",
  components: {    
    topbar,
    searchbar,
    maincontent,
    backtop
  },  
  data() {
    return {
      isstart : 1
    };
  },
  mounted() {
    this.justfiypage();
  },
  created() {    
    window.addEventListener("scroll", this.scrollchange);    
    this.isstart = 1
  },
  destroyed() {
    this.isstart = 0
    window.removeEventListener("scroll", this.scrollchange)
    //console.clear();
  },
  methods: {
    justfiypage() {
      //console.log(this.$route.path) //哇终于弄好了心累，在vue中使用原生js实现功能生js实现功能··我还在学习vue的过程中，有些功能还是用原生比较熟练··
      //在离开页面的时候绑定在全局的windows上的方法会出问题··找不到被校会的DOM节点··尝试添加keep-alive无果，最终发现在vue中使用原生js很不合适有太多的bug
      //我再也不在vue里写原生了····哈哈哈
      if (this.$route.path == '/home' || this.$route.path == '/') {
        if(this.isstart = 1) {
          this.sliderchange()
        }
        window.addEventListener("scroll", this.scrollchange);        
        window.addEventListener('load' ,this.sliderchange);
      }     
    },
    sliderchange() {
      var slider = document.querySelector(".slider");
      var ul = slider.children[0];
      //获取盒子宽度
      var w = slider.offsetWidth;
      var ol = slider.children[1];
      //利用定时器：
      var index = 0;
      var timer = setInterval(function() {
        index++;
        var translatex = -index * w;
        ul.style.transition = "all .5s";
        ul.style.transform = "translateX(" + translatex + "px)";
      }, 2000);

      ul.addEventListener("transitionend", function() {
        if (index >= 3) {
          index = 0;
          //console.log(index);
          ul.style.transition = "none";
          var translatex = -index * w;
          ul.style.transform = "translateX(" + translatex + "px)";
        } else if (index < 0) {
          index = 2;
          ul.style.transition = "none";
          var translatex = -index * w;
          ul.style.transform = "translateX(" + translatex + "px)";
        }
        //小圆点跟随变化 classList
        ol.querySelector(".current").classList.remove("current");
        ol.children[index].classList.add("current");
      });
      //手指滑动轮播图
      var startX = 0;
      var moveX = 0;
      var flag = false; //严谨操作
      ul.addEventListener("touchstart", function(e) {
        startX = e.targetTouches[0].pageX;
        //手指触摸清除定时器
        clearInterval(timer);
      });
      ul.addEventListener("touchmove", function(e) {
        moveX = e.targetTouches[0].pageX - startX;
        var translatex = -index * w + moveX;
        ul.style.transform = "translateX(" + translatex + "px)";
        flag = true;
        e.preventDefault();
      });
      ul.addEventListener("touchend", function() {
        if (flag == true) {
          //如果距离够大
          if (Math.abs(moveX) > 50) {
            //如果右滑播放上一张
            //如果左滑播放下一张
            if (moveX > 0) {
              index--;
            } else {
              index++;
            }
            var translatex = -index * w;
            ul.style.transition = "all .3s";
            ul.style.transform = "translateX(" + translatex + "px)";
          } else {
            var translatex = -index * w;
            ul.style.transition = "all .1s";
            ul.style.transform = "translateX(" + translatex + "px)";
          }
        }
        //重启定时器
        clearInterval(timer);
        timer = setInterval(function() {
          index++;
          var translatex = -index * w;
          ul.style.transition = "all .5s";
          ul.style.transform = "translateX(" + translatex + "px)";
        }, 2000);
      });
    },
    scrollchange() {   
      var search = document.querySelector(".search-wrap");
      var specialbar = document.querySelector(".specialbar");
      var backtop = document.querySelector(".backtops");
      var topflag = 0;
      var closedie = document.querySelector("#closedie");
      var topbar = document.querySelector("#Topbar");     
      /* closedie.addEventListener("touchend", function bbbs() {
        topbar.style.display = "none";
        slider.style.top = 6 + "px";
        topflag = 1;
        search.style.top = "0";
      }); */

      //console.log(search);
      window.addEventListener("scroll", function handled() {
        if (window.pageYOffset >= 20) {
          search.style.top = "0";
        }
        if (window.pageYOffset < 20 && topflag == 0) {
          search.style.top = "45px";
          //console.log(window.pageYOffset);
          //console.log(topflag);
        }
        if (window.pageYOffset >= 2000) {
          backtop.style.display = "block";
        } else {
          backtop.style.display = "none";
        }
        if (window.pageYOffset > 430) {
          specialbar.style.position = "fixed";
          (specialbar.style.top = -211 + "px"),
            (specialbar.style.backgroundColor = "rgba(53, 53, 53, .8)");
        } else {
          specialbar.style.position = "";
          (specialbar.style.top = null),
            (specialbar.style.backgroundColor = "rgb(53, 53, 53)");
        }
         if (window.pageYOffset > 5000) {
        }
      });
    /*   window.addEventListener("scroll", function() {
        
      }); */
      backtop.addEventListener("touchend", function aaaaaa() {
        window.scroll(0, 0);
      });
     /*  window.addEventListener("scroll", function() {
        //console.log(window.pageYOffset)
        
      });
      window.addEventListener("scroll", function() {
       
      }); */

    }
  }
};
</script>


<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  width: 100%;
  min-width: 320px;
  max-width: 640px;
  margin: 0 auto;
  font-size: 14px;
  font-family: -apple-system, Arial, Helvetica, sans-serif;
  color: #666;
  line-height: 1.5;
}
</style>
