<template>
  <div class="choosebar">
    <div class="specialbar">
      <div
        v-for="(item,index) in titles"
        :key="index"
        class="choosebaritem"
        :class=" { active: index === currentindex } "
        @click="itemClick(index)"
      >
        <span>{{item}}</span>
      </div>
    </div>
  </div>
</template>


<script>
//aa
export default {
  name: "Choosebar",
  props: {
    titles: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      currentindex: 0
    };
  },
  methods: {
    itemClick(index) {
      this.currentindex = index;
      this.$emit('chooseClick', index);
    }
  }
};
</script>


<style>
.specialbar {
  height: 40px;
  background-color: rgb(53, 53, 53);
  margin-top: 255px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  color: rgb(246, 246, 246);
/*   border-radius: 15px; */
  box-sizing: content-box;
  width: 100%;
}
.choosebaritem {
  flex: 1;
  text-align: center;
}
.active {
  color: skyblue;
  font-weight: 600;
}
</style>
